import React,{ useState } from 'react'
import { Link, useHistory } from 'react-router-dom'

const Login =  (props) => {

    const [credentials,setcredentials] = useState({email:"",password:""})
    let history = useHistory();
   

    const handleSubmit = async(e)=>{
        e.preventDefault();
        const response = await fetch("http://localhost:5000/api/auth/login", {
            method: 'POST',
            headers: {
              'Content-Type': 'application/json'
            },
            body: JSON.stringify({email: credentials.email,password:credentials.password})
          });
          const json = await response.json()
          console.log(json);
          if(json.success){
              //redirect
              localStorage.setItem('token',json.authtoken);
              props.showAlert("Logged in Successfully","success")
              history.push("/");
              
          }
          else{
            props.showAlert("invalid information","danger")
          }
        }

        const onChange = (e)=>{
           setcredentials({...credentials, [e.target.name]: e.target.value})
        }



    return (
        <div>
            <h2>Login to continue </h2>
                    <form onSubmit={handleSubmit}>
            <div className="mb-3">
                <label htmlFor="email" className="form-label">Email address</label>
                <input type="email" name="email" className="form-control" value={credentials.email} onChange={onChange} id="email" aria-describedby="emailHelp"/>
               
            </div>
            <div className="mb-3">
                <label htmlFor="password" className="form-label">Password</label>
                <input type="password" className="form-control" id="password" name="password" value={credentials.password} onChange={onChange}/>
            </div>
            
           
           
            <button type="submit" className="btn btn-primary" >Submit</button>
            <div className="container"><br />
                <Link className="" to="/signup">Not Have An Acoount.</Link>
            </div>
            </form>
        </div>
    )
}

export default Login
